
.js-menu {
  float: left;
  width: 220px;
  color: #4c4c4c;
  background: #fff;
  padding: 0 10px;
}
.menu-option {
  padding: 20px 0 5px 0;
  border-bottom: 1px solid #f2f2f5;
}
.menu-option-tit {
  font-weight: bold;
}
.menu-option-ul {
  margin: 15px 0 0 0;
  width: 208px;
}
.menu-option-ul li a {
  font-size: 12px;
}
.menu-option-ul li {
  width: 47%;
  float: left;
  padding: 5px 15px;
  margin: 0 3% 15px 0;
  text-align: center;
  background: #f4f4f4;
  cursor: pointer;
}
.menu-option-ul li.active {
  background: #3089dc;
}
.menu-option-ul li.active a {
  color: #fff;
}
.menu-option-color {
  width: 210px;
  margin: 15px 0 0 0;
}
.menu-option-color li {
  width: 30px;
  height: 22px;
  overflow: hidden;
  float: left;
  border-radius: 4px;
  margin: 0 12px 15px 0;
}
.menu-option-color .red {
  background: #f00;
}
.menu-option-color .whiteBlack {
  background: url("../../static/images/whiteBlack.png") no-repeat;
  background-size: 100% 100%;
}
.menu-option-color .black {
  background: #000;
}
.menu-option-color .greed {
  background: #0f0;
}
.menu-option-color .blue {
  background: #00f;
}
.menu-option-color .pink {
  background: #ffc0cb;
}
.menu-option-color .brown {
  background: #a67d3d;
}
.menu-option-color .gray {
  background: #808080;
}
.menu-option-color .color {
  background: url("../../static/images/color.png") no-repeat;
  background-size: 100% 100%;
}
.menu-option-color .purple {
  background: #808080;
}
.menu-option-color .orange {
  background: #ffa500;
}
.menu-option-color .yellow {
  background: #ff0;
}


