
.header-area {
  width: 100%;
  height: 170px;
  margin: 0 auto;
  background: #fff;
}
.header-area-search {
  width: 1200px;
  height: 128px;
  margin: 0 auto;
}
.header-area-nav {
  width: 1200px;
  height: 42px;
  margin: 0 auto;
  background: #3089dc;
}
.header-nav-ul {
  height: 100%;
}
.header-nav-ul li {
  float: left;
  line-height: 42px;
  padding: 0 25px;
  display: block;
  cursor: pointer;
}
.header-nav-ul li:hover {
  background: #1170bc;
}
.header-nav-ul li a {
  color: #fff;
  display: block;
}


