
.js-template {
  background: #fff;
  margin: 0 0 0 235px;
}
.js-template .template-list {
  padding: 15px 0 0 0;
}
.js-template .template-list-li {
  width: 220px;
  overflow: hidden;
  float: left;
  border: 1px solid #d4d4d4;
  margin: 0 0 15px 16px;
}
.js-template .template-list-li img {
  width: 220px;
  height: 350px;
}
.js-template .list-tit {
  padding: 5px 5px 0;
  line-height: 20px;
  font-size: 12px;
}


